import { loadManifest } from '@angular-architects/module-federation';
import { environment } from './environments/environment';

function getManifestPath(): string {
  if (environment.envName === 'qa') {
    return 'projects/mf-host/src/assets/module-federation.manifest.qa.json';
  }

  return environment.production
    ? 'projects/mf-host/src/assets/module-federation.manifest.prod.json'
    : 'projects/mf-host/src/assets/module-federation.manifest.dev.json';
}

loadManifest(getManifestPath())
  .then(manifest => {
    console.log('Manifest loaded:', environment.envName);
    console.log('Manifest loaded:', manifest);
    return import('./bootstrap');
  })
  .catch(err => console.error('Error loading manifest or bootstrap:', err));
